/* Global Styles */
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  background-color: #f4f6f8;
  color: #333;
}

/* Contact Container */
.contact-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 50px 20px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Contact Title */
.contact-title {
  text-align: center;
  font-size: 2.5rem;
  color: #1e90ff;
  margin-bottom: 20px;
}

/* Contact Intro */
.contact-intro {
  text-align: center;
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 40px;
}

/* Form Styles */
.contact-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  font-size: 1rem;
  color: #333;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  color: #333;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: border 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #1e90ff;
  outline: none;
}

textarea {
  resize: vertical;
  height: 150px;
}

/* Submit Button */
.submit-btn {
  background-color: #1e90ff;
  color: white;
  padding: 15px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #0b72cc;
}

.submit-btn:focus {
  outline: none;
}